.pop-up__tooltip-title {
  all: unset;
  color: #000;
  text-align: center;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
  max-width: 358px;
  margin-bottom: 60px;
}

@media all and (min-width: 320px) and (max-width: 767px) {
  .pop-up__tooltip-title {
    font-size: 1.25rem;
  }
}
